import React from 'react';
import s from './ProfileInfo.module.css'
import Preloader from "../../common/Preloader/Preloader";
import paris_airport from "../../../assets/parisAirport.jpg";
import ProfileStatusWithHooks from "../ProfileStatusWithHooks";
import styles from "../../Users/Users.module.css";
import userPhoto from "../../../assets/userPhoto.png";

const ProfileInfo = ({profile, status, updateStatus,...props}) => {

    if (!profile) {
        return <Preloader/>
    }

    return (
        <div>
            <div className={s.descriptionBlock}>
                <div className={s.photoDescriptionBlock}>
                <img src={paris_airport}/>
                </div>
                <div className={s.elementDescriptionBlock}>
                <img src={profile.photos.large || userPhoto}/>
                </div>
            </div>

            <div className={s.descriptionBlock}>
                <div className={s.elementDescriptionBlock}>
                    <span>{profile.lookingForAJobDescription ? 'Учеба(работа) : ' : null}
                        {profile.lookingForAJobDescription}</span>
                </div>
                <div className={s.elementDescriptionBlock}>
                    <span>{profile.aboutMe? 'О себе : ' : null }{profile.aboutMe}</span>
                </div>
                <ProfileStatusWithHooks status={status} updateStatus={updateStatus} />
            </div>
        </div>
    );
}

export default ProfileInfo;