import React from 'react';
import Profile from "./Profile";
import {connect} from "react-redux";
import {getStatus, updateStatus} from "../../redux/profile-reducer";
import {withRouter} from "react-router-dom";
import {getUserProfile} from "../../redux/profile-reducer";
import {compose} from "redux";


class ProfileContainer extends React.Component {

    componentDidMount() {
        let userId = this.props.match.params.userId;
        if(!userId) {
            userId = this.props.auterisedUserId;
        }if (!userId) {
            this.props.history.push("/login");                   /*рендеринг с помщью push урок 80*/
        }
        this.props.getUserProfile(userId);
        this.props.getStatus(userId);
    }

    render() {
        return (
            <Profile {...this.props}                            /*спред-оператор используем обязательно!*/
                     profile={this.props.profile}
                     status={this.props.status}
                     updateStatus={this.props.updateStatus}/>

            );
    }
}


let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    auterisedUserId : state.auth.userId,
    isAuth: state.auth.isAuth
});



export default compose (                                                              /*compose урок 70*/
    connect(mapStateToProps,{getUserProfile, getStatus, updateStatus}),
    withRouter,
    // withAuthRedirect                                                              /*HOC урок 69*/
)(ProfileContainer)