import React from 'react';
import s from './MyPosts.module.css'
import Post from './Post/Post'
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../utils/validators/validators";
import {Textarea} from "../../common/FormsControls/FormsControls";

const maxLength10 = maxLengthCreator(10);
const styleFormError = {
    border: 'solid red 2px',
    color:'red'
}

const AddNewPostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            {/*<div>*/}
            {/*<textarea onChange={onPostChange} ref={newPostElement} value={props.newPostText}/>*/}
            <Field name={"newPostText"}
                   component={Textarea}
                   placeholder={"Your post"}
                   validate={[required, maxLength10]}/>            {/*валидатор урок 77*/}
            {/*</div>*/}
            <div>
                {/*<button onClick={onAddPost}>Add post</button>*/}
                <button>Add post</button>
            </div>
        </form>
    )
}


window.props = [];

class MyPosts extends React.Component {
    render() {
        window.props.push(this.props);

        let postsElements = this.props.posts.map(p => <Post post={p.post} key={p.id} lakesCount={p.likesCount}/>);

        // let newPostElement = React.createRef();

        let onAddPost = (values) => {                /*функция-обработчик, которую передаем в redux-form (урок 76)*/
            this.props.onAddPost(values.newPostText);

            // let action = {type: 'ADD-POST'};
            // props.dispatch(addPostActionCreator());
        }

        // let onPostChange = () => {
        //     let text = newPostElement.current.value;
        //     props.onPostChange(text);

        // let action = {type: 'UPDATE-NEW-POST-TEXT', newText: text};
        // let action = updateNewPostTextActionCreator(text);
        // props.dispatch(action);
        // }


        return (
            <div>
                <div className={s.postsBlock}>
                    <h3>My Posts</h3>
                </div>
                <AddNewPostReduxForm onSubmit={onAddPost}/>
                <div className={s.posts}>
                    {postsElements}
                </div>
            </div>

        );
    }
}


const AddNewPostReduxForm = reduxForm ({form: 'ProfileAddNewPostForm'})(AddNewPostForm)



export default MyPosts;